.container.testimonials_container{
    width: 40%;
    

}

.client_avatar {
    width: 4rem;
    aspect-ratio: 1/1;
    overflow: hidden;
    border-radius: 50%;
    margin: 0 auto 1rem;
    border: 0.4rem solid var(--color-primary-varient);
}

.testimonial{
    background: var(--color-bg-variant);
    text-align: center;
    padding: 2rem;
    border-radius: 2rem;
    user-select: none;

}

.client_review {
    color:var(--color-light);
    text-align: center;
    font-weight: 300;
    display: block;
    width: 80%;
    margin: 0.8rem auto 0;


}


/*=============MEDIA QUERIES (MEDIUM DEVICES)=========*/
@media screen and (max-width:1024px) {
.container .container_testimonial {
    width: 60%;

}
    
}


/*=============MEDIA QUERIES (SMALL DEVICES)=========*/
@media screen and (max-width:600px) {
.container .container_testimonial {
    width: var(--container-width-sm);
    
}

.client_review{
    width: var(--container-width-sm);
}
    
}